import socket from "~/plugins/socket.io.js";
import { mapGetters, mapActions } from "vuex";

export default {
  data() {
    return {
      usersCount: 0,
    };
  },
  beforeMount() {
    socket.on("calc users", (num) => {
      this.usersCount = num;
    });
  },
  mounted() {
    socket.emit("getUsers");
  },
  computed: {
    ...mapGetters(["user"]),
  },
  methods: {
    ...mapActions(["out"]),
    logout() {
      this.out();
      socket.emit("logout");
    },
  },
};
